/* Base styles for all screen sizes */
.App {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    background-color: #AD8546;
}

.hero-container {
    background-color: #206CA2;
    margin: 1em;
    padding: 1em; /* Added the missing semicolon */
}

.hero {
    background-color: aliceblue;
    background-image: url(./assets/multas.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width: 100vw;
    height: 80vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.title {
    font-size: 6em;
    font-weight: 400;
}

.text {
    font-size: 2em;
    font-weight: 400;
}

.text2 {
    font-size: 2.5em;
    font-weight: 400;
}

* {
    margin: 0;
    padding: 0;
}

/* Media query for smaller screens (adjust the max-width as needed) */
@media screen and (max-width: 768px) {
    .title {
        font-size: 4em; /* Adjust the font size for smaller screens */
    }

    .text,
    .text2 {
        font-size: 1.5em; /* Adjust the font size for smaller screens */
    }
}
